import React, { useEffect, useRef, useState } from "react";
import { EditorState } from "@codemirror/state";
import { EditorView, keymap } from "@codemirror/view";
import { defaultKeymap } from "@codemirror/commands"; // Import default key bindings
import { javascript } from "@codemirror/lang-javascript"; // Language extension example
import { html } from "@codemirror/lang-html"; // Import HTML extension
import { css } from "@codemirror/lang-css"; // Import CSS extension

const myTheme = EditorView.theme(
  {
    "&.cm-editor": {
      caretColor: "white",
    },
    ".cm-content": {
      color: "white",
      backgroundColor: "#1d1e22",
    },
  },
  { dark: true }
);

const Editor = ({ language, value, onChange }) => {
  const editorContainer = useRef(null);
  const editorView = useRef(null);

  // Local state to track the current value
  const [editorValue, setEditorValue] = useState(value);

  useEffect(() => {
    if (!editorContainer.current) return;

    if (!editorView.current) {
      // Choose language extension based on the 'language' prop
      let languageExtension;
      switch (language) {
        case "javascript":
          languageExtension = javascript();
          break;
        case "html":
          languageExtension = html();
          break;
        case "css":
          languageExtension = css();
          break;
        default:
          languageExtension = javascript(); // Fallback or default language
      }

      const startState = EditorState.create({
        doc: editorValue,
        extensions: [
          languageExtension, // Replace with the appropriate language extension
          keymap.of(defaultKeymap),
          myTheme,
          EditorView.updateListener.of((update) => {
            if (update.docChanged) {
              const newValue = update.state.doc.toString();
              setEditorValue(newValue); // Update local state
              onChange(newValue); // Call external onChange
            }
          }),
        ],
      });

      editorView.current = new EditorView({
        state: startState,
        parent: editorContainer.current,
      });
    }

    return () => {
      if (editorView.current) {
        editorView.current.destroy();
        editorView.current = null;
      }
    };
  }, [language, onChange]);

  // Only update editor content if the value prop changes externally
  useEffect(() => {
    if (editorView.current) {
      const currentValue = editorView.current.state.doc.toString();
      if (value !== currentValue) {
        editorView.current.dispatch({
          changes: {
            from: 0,
            to: currentValue.length,
            insert: value,
          },
        });
      }
    }
  }, [value]);

  return (
    <div
      ref={editorContainer}
      style={{
        height: "300px",
        backgroundColor: "#1d1e22",
        color: "white",
        fontSize: "1.2em",
        borderRadius: "4px",
        border: "1px solid #333",
        overflow: "hidden",
      }}
    />
  );
};

export default Editor;
