// src/App.js
import React, { useState } from "react";
import { Grid, Paper, Typography } from "@mui/material";
import Editor from "./components/Editor";
import Preview from "./components/Preview";

function App() {
  const [html, setHtml] = useState("");
  const [css, setCss] = useState("");
  const [js, setJs] = useState("");

  return (
    <Grid container spacing={1} style={{ height: "100vh" }}>
      <Grid item xs={4}>
        <Paper
          style={{
            padding: "10px",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Typography
            variant="h6"
            gutterBottom
            style={{ background: "rgb(255 60 65)", padding: "4px" }}
          >
            HTML
          </Typography>
          <Editor language="html" value={html} onChange={setHtml} />
        </Paper>
      </Grid>
      <Grid item xs={4}>
        <Paper
          style={{
            padding: "10px",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Typography
            variant="h6"
            gutterBottom
            style={{ background: "rgb(14 190 255)", padding: "4px" }}
          >
            CSS
          </Typography>
          <Editor language="css" value={css} onChange={setCss} />
        </Paper>
      </Grid>
      <Grid item xs={4}>
        <Paper
          style={{
            padding: "10px",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Typography
            variant="h6"
            gutterBottom
            style={{ background: "rgb(252 208 0)", padding: "4px" }}
          >
            JavaScript
          </Typography>
          <Editor language="javascript" value={js} onChange={setJs} />
        </Paper>
      </Grid>
      <Grid item xs={12}>
        <Paper
          style={{
            padding: "10px 10px 2px 10px",
            display: "flex",
            flexDirection: "column",
            fontSize: "1.2em",
          }}
        >
          <Typography
            variant="h6"
            gutterBottom
            style={{ background: "gray", padding: "5px" }}
          >
            Preview
          </Typography>
          <Preview html={html} css={css} js={js} />
        </Paper>
      </Grid>
    </Grid>
  );
}

export default App;
